@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Baloo Tammudu 2", sans-serif;
}

.main_div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
}

.videos {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-gap: 2rem;
}

.videos .recent_uploads {
  text-transform: capitalize;
  border-bottom: 1px solid rgb(190, 190, 190);
}
.video {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.video .thumb_img {
  width: 75%;
  height: 100%;
}

.video img {
  width: 100%;
  height: 100%;
}
.details {
  padding: 0 1rem;
}
.desc {
  border-bottom: 1px solid rgb(190, 190, 190);
  padding-bottom: 0.5rem;
}

.video h2 {
  margin-top: 1rem;
}

.video p:nth-child(2),
.video p:nth-child(3),
.video p:nth-child(4),
.video p:nth-child(5) {
  color: #948c81;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

.header {
  height: 75vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.about_head {
  text-align: center;
  border-bottom: 1px solid rgb(190, 190, 190);
}

@media (max-width: 768px) {
  .video {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .video .thumb_img {
    width: 100%;
  }
  .header {
    height: 87vh;
  }
}

